@import url('https://fonts.googleapis.com/css?family=Lato:300,400,700,900&display=swap');

.cointenerWorks {
  background-color: #ffffff;
  display: flex;
}
.cointenerCardsProyects {
  width: 80%;
}
.spanContact {
  color: #6e07f3;
}

.titleWorks {
  text-align: center;
  margin: 0;
  font-family: 'Lato';
  font-size: 30px;
  color: #141c3a;
  padding: 0px;
  margin: 50px 0 60px 0;
}
::-webkit-scrollbar {
  background: #2e2e2e;
  width: 8px;
}
::-webkit-scrollbar-thumb {
  background-color: #00fa77;
}
@media only screen and (max-width: 650px) {
  .cointenerWorks {
    display: inline-block;
    height: auto;
  }
  .cointenerCardsProyects {
    width: 100%;
  }
}
@media only screen and (max-width: 425px) {
  .titleWorks {
    font-size: 25px;
  }
}
