@import url('https://fonts.googleapis.com/css?family=Lato:300,400,700,900&display=swap');

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
html,
body {
  height: 100%;
}
.cointenerAbout {
  background-color: #ffffff;
  display: flex;
  position: absolute;
  width: 100vw;
  height: 100vh;
}
.cointenerTextAbout {
  text-align: center;
  margin: auto;
  display: grid;
  grid-template-columns: repeat(1, 50%);
  justify-content: center;
  width: 80%;
}
.photoMiri {
  width: 40%;
  margin: 1.5rem auto;
  padding: 0px 0 0px 0;
}
.titleAbout {
  font-family: 'Lato';
  font-size: 30px;
  color: #141c3a;
}
.aboutMe {
  color: #b3b3b3;
  font-family: 'Lato';
  font-size: 16px;
  text-align: left;
  padding: 0 0 40px 0;
}
.cointenerButtonsAbout {
  display: flex;
  align-items: center;
  justify-content: center;
}
.buttonAboutContact {
  margin: 2rem 2rem 80px 0;
  padding: 10px;
  color: #6e07f3;
  width: 90%;
  border: 1px solid #6e07f3;
  font-weight: bold;
  text-transform: uppercase;
  letter-spacing: 0.1em;
  white-space: nowrap;
  position: relative;
  overflow: hidden;
  transition: color 0.4s ease;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 10px;
  cursor: pointer;
  position: relative;
}

.buttonAboutContact::before {
  content: '';
  position: absolute;
  top: 0;
  left: -100%;
  width: 100%;
  height: 100%;
  background-color: #6e07f3;
  transition: left 0.4s;
  z-index: -1;
}

.buttonAboutContact:hover::before {
  left: 0;
}
.buttonAboutContact:hover .photoarrow {
  filter: brightness(0) invert(1);
}

.buttonAboutContact:focus {
  border-color: #6e07f3; 
  outline: none; 
}

.photoarrow {
  width: 30px;
  height: 20px;
  transition: filter 0.4s ease;
  filter: brightness(0) invert(30 0 0 #f0bce7);
}

.buttonAboutContact:hover {
  color: #ffff;
}

@media only screen and (max-width: 920px) {

  .photoMiri {
    width: 60%;
  }
  .cointenerTextAbout {
    grid-template-columns: repeat(1, 80%);
  
  }
}

@media only screen and (max-width: 780px) {
  .titleAbout {
    font-size: 25px;
  }

  .aboutMe {
    font-size: 14px;
  }
  .cointenerTextAbout {
    grid-template-columns: repeat(1, 80%);
  }
}

@media only screen and (max-width: 650px) {
  .cointenerTextAbout {
    margin-top: 40px;

    height: 70vh;
  }
  .cointenerAbout {
    display: inline-block;
    height: auto;
  }

  .photoMiri {
    width: 90%;
    margin: 1.5rem auto;
    padding: 0px 0 0px 0;
  }

  .buttonAboutContact {
    width: 100%;
    margin: 0 5px 20px 0;
  }
  .cointenerButtonsAbout {
    display: inline-block;
    height: auto;
  }
  .cointenerTextAbout {
    display: absolute;
    width: 40vh;
    grid-template-columns: repeat(1, 80%);
  }

  .titleAbout {
    font-size: 25px;
  }

  @media only screen and (max-width: 370px) {
    .photoMiri {
      width: 60%;
      margin: 1.5rem auto;
      padding: 0px 0 0px 0;
    }
    .buttonAboutContact {
      width: 100%;
      margin: 0 5px 20px 0;
    }
    .titleAbout {
      font-size: 23px;
    }
  }
}
